.profile__add-button {
  width: 150px;
  height: 50px;
  background-color: #000;
  color: white;
  border: solid #ffffff;
  font-size: 35px;
}

.profile__add-button:hover {
  opacity: 0.6;
  cursor: pointer;
}

@media screen and (max-width: 640px) {
  .profile__add-button {
    width: 282px;
    height: 50px;
    margin: 0;
  }
}
