.popup__container {
  display: flex;
  flex-direction: column;
  position: fixed;
  left: 50%;
  top: calc(50% + 28px);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  z-index: 10;
  background: #ffffff;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
  border-radius: 10px;
  padding: 34px 36px;
  justify-content: center;
  align-content: center;
  margin: 0;
  max-width: 360px;
}

@media screen and (max-width: 640px) {
  .popup__container {
    max-width: 240px;
    top: calc(50% - 20px);
    padding: 25px 22px;
  }
}
