.profile__name {
  font-family: "Inter", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 42px;
  line-height: 1.14;
  color: #ffffff;
  margin: 0;
  max-width: 373px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

@media screen and (min-width: 640px) and (max-width: 910px) {
  .profile__name {
    font-size: 22px;
    line-height: 27px;
  }
}

@media screen and (max-width: 640px) {
  .profile__name {
    font-size: 22px;
    line-height: 27px;
    max-width: 200px;
  }
}
