.profile {
  margin: 40px auto 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-width: 880px;
}

@media screen and (min-width: 640px) and (max-width: 910px) {
  .profile {
    min-width: 90%;
    max-width: 600px;
  }
}

@media screen and (max-width: 640px) {
  .profile {
    display: flex;
    flex-direction: column;
    margin: 42px auto 36px;
    min-width: 300px;
  }
}
