.elements {
  max-width: 880px;
  display: flex;
  flex-wrap: wrap;
  row-gap: 20px;
  column-gap: 17px;
  margin: 0 auto 67px;
}

@media screen and (min-width: 641px) and (max-width: 900px) {
  .elements {
    width: 640px;
    justify-content: center;
  }
}

@media screen and (max-width: 640px) {
  .elements {
    margin-bottom: 50px;
    justify-content: center;
  }
}
